@import '../../utilities/init';
@import '../../components/pagination/pagination';
@import '../../components/range/range';
@import '../../components/checkboxes/checkbox-poor/checkbox-poor';
@import '../../components/checkboxes/checkbox-rich/checkbox-rich';
@import '../../components/checkboxes/checkbox-expandable/checkbox-expandable';
@import '../../components/field-dropdown/field-dropdown';
@import '../../components/room/room';
@import '../../components/main-header/main-header';
@import '../../components/main-footer/main-footer';

.rooms-page {
    padding: 30px 0 70px;

    .container {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
}

// FILTER
.filter {
    width: 266px;
    margin-right: 20px;

    .dropdown-wrapper {
        margin-top: 22px;
    }
}

.filter__btn-mobile--show {
    box-sizing: border-box;
    display: none;
    width: 100%;
    padding: 10px 20px;
    margin-bottom: 20px;

    font-weight: bold;
    font-size: 16px;
    color: #fff;

    text-align: center;
    background: linear-gradient(180deg, $purple-color-100 0%, #8ba4f9 100%);
}

.filter__btn-mobile--close {
    display: none;
    position: fixed;
    z-index: 9;
    top: 0;
    right: 15px;

    width: 40px;
    height: 40px;

    background: $primary-color-75;
    border-radius: 0 0 0 5px;

    &::before,
    &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;

        width: 20px;
        height: 3px;

        background-color: #fff;
    }

    &::before {
        transform: translate(-50%, -50%) rotate(45deg);
    }

    &::after {
        transform: translate(-50%, -50%) rotate(-45deg);
    }
}

.filter__group {
    padding: 0;
    margin: 0 0 20px;

    border: none;

    .c-range__group {
        display: inline-block;
        margin: 10px 0 7px;
    }

    .c-range__label {
        margin: 0;
    }

    .c-range__item {
        margin-bottom: 20px;
    }

    .c-range__description {
        display: inline-block;
        margin-top: 10px;
    }
}

.filter__label {
    display: block;
    margin: 0 0 4px;

    &::after {
        top: 40px;
        right: 18px;
    }
}

.filter__field {
    width: 100%;
    height: 46px; //for ie
    padding: 10px 30px 10px 15px;
}

.filter__title {
    display: block;
    padding: 0;
    margin: 0 0 16px;

    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    color: #1f2041;

    text-transform: uppercase;
}

.filter__list {
    margin: 0 0 13px;
    padding: 0;

    list-style-type: none;

    .filter__item + .filter__item {
        margin-top: 13px;
    }

    .c-checkbox-poor__label {
        padding-left: 29px;
    }

    .c-checkbox-rich__label {
        margin-bottom: -4px;
    }

    .c-checkbox-rich__description {
        margin-top: 7px;
    }
}

.c-checkbox-expandable__group {
    margin-top: 29px;

    .c-checkbox-expandable__title {
        width: 100%;
        margin-bottom: 19px;

        &::after {
            top: 5px;
            right: 18px;
        }
    }
}

// ROOMS
.rooms {
    width: 834px;
}

.rooms__title {
    margin: 0 0 20px;

    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    color: $primary-color-100;

    letter-spacing: -0.5px;
}

.rooms__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 -10px 19px 0;
    padding: 0;

    list-style-type: none;
}

.rooms__item {
    margin: 0 10px 19px 0;
}

.rooms-pagination__list {
    width: 240px;
    margin: 0 0 10px 290px;
}

.rooms-pagination__description {
    width: 240px;
    margin: 0 auto;
}

// Media
@media screen and (max-width: 1159px) {
    .filter {
        width: 100%;
        margin-right: 0;
    }

    .filter__btn-mobile--show {
        display: inline-block;
    }

    .filter__form {
        position: fixed;
        z-index: 10;
        top: 0;
        bottom: 0;
        right: 0;
        left: 110%;

        padding: 10px;

        background: #fff;
        overflow: scroll;
        transition: left ease 1s;
    }

    .c-checkbox-expandable__group {
        margin-bottom: 0;
    }

    .rooms {
        width: 100%;
    }

    .rooms__item {
        width: 32%;
    }

    .c-room__wrapper,
    .c-room__container,
    .c-room__img {
        width: 100%;
        height: auto;
    }

    .rooms-pagination__list {
        margin: 0 auto 10px;
    }
}

@media screen and (max-width: 900px) {
    .rooms__item {
        width: 48%;
    }
}

@media screen and (max-width: 591px) {
    .rooms__list {
        margin-right: 0;
    }

    .rooms__item {
        width: 100%;
        margin-right: 0;
    }

    .c-room__wrapper,
    .c-room__container,
    .c-room__img {
        width: 100%;
        height: auto;
    }
}